<template>
  <div class="index">
    <!--<h2>{{msg}}</h2>-->
    <el-button @click="getAjax">Ajax</el-button>
    <el-button @click="getAxios">Axios</el-button>
  </div>
</template>

<script>
/* eslint-disable no-console */

import { getRandomJoke, getNovelSearch, getNovelDetail } from '@/api/apiopen'
import $axios from 'axios'
import $$ from 'jquery'

export default {
  name: 'index',
  data () {
    return {
      msg: '我是主页面',
      isCollapse: true
    }
  },
  mounted () {

  },
  methods: {
    getAjax () {
      $$.ajax({
        type: 'GET',
        // async: true,    //是否异步
        url: 'http://meiriyikan.cn/api/json.php',
        dataType: 'json',
      }).then(res=>{
        console.log('ajax 返回数据', res)
      }).catch(err=>{
        console.log('ajax 失败', err)
      })
    },
    getAxios () {
      $axios
        .get('http://meiriyikan.cn/api/json.php')
        .then(res=> {console.log('axios 返回数据', res)})
        .catch(res=> {console.log('axios失败', res)})
    }
  }
}
</script>
